<template>
    <div class="creditCertification">
        <div class="tip text-center">按顺序完成以下验证</div>
        <ul>
            <li class="flex flex-between" @click="openCertificat('person')">
                <div class="left">
                    <div>个人基础信息认证</div>
                    <div class="sec">必须填写本人资料</div>
                </div>
                <div
                    class="right"
                    :class="{pass: user.certification.person}"
                >{{user.certification.person ? '认证完成' : '待认证 >'}}</div>
            </li>
            <li class="flex flex-between" @click="openCertificat('img')">
                <div class="left">
                    <div>人证核查</div>
                </div>
                <div
                    class="right"
                    :class="{pass: user.certification.img}"
                >{{user.certification.img ? '认证完成' : '待认证 >'}}</div>
            </li>
            <li class="flex flex-between" @click="openCertificat('bank')">
                <div class="left">
                    <div>银行卡认证</div>
                </div>
                <div
                    class="right"
                    :class="{pass: user.certification.bank}"
                >{{user.certification.bank ? '认证完成' : '待认证 >'}}</div>
            </li>
        </ul>
        <cube-button
            class="code-btn"
            @click="getMoney"
            :style="[appConfig.mainBackground]"
            :primary="true"
        >立即借款</cube-button>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { brigeNativeByJavascript } from '../api/Bridge.js'
export default {
    data() {
        return {}
    },
    methods: {
        openCertificat(cer) {
            if (this.user.certification[cer]) return
            if (cer === 'person') {
                this.$router.push('/personInfo')
            } else if (cer === 'img') {
                this.$router.push('/idCardAuth')
            } else if (cer === 'bank') {
                this.$router.push('/bankAuth')
            }
        },
        getMoney() {
            if (this.user.certification.person && this.user.certification.img && this.user.certification.bank) {
                // this.$router.push('/loanCalc')
                this.$router.go(-1)
                // brigeNativeByJavascript('closeRefundApplication')
            } else {
                this.$store.commit('setMsg', '请先完成认证')
            }
        }
    },
    computed: {
        ...mapState(['certification', 'user', 'appConfig'])
    },
    beforeCreate() {
        document.title = '信用认证'
    }
}
</script>

